@import url("https://fonts.googleapis.com/css?family=Amatic+SC");
#cooking {
  width: 370px;
  height: 450px;
  position: relative;
  margin: 0 auto;
  top: 0;
  overflow: hidden; }
  #cooking h1 {
    position: absolute;
    top: 10%;
    left: 0;
    margin: 0 auto;
    width: 370px;
    text-align: center;
    font-family: 'Amatic SC';
    font-size: 20px;
    color: #333;
    opacity: .75;
    animation: pulse 2.5s linear infinite; }
  #cooking .bubble {
    position: absolute;
    border-radius: 100%;
    box-shadow: 0 0 0.25vh #4d4d4d;
    opacity: 0; }
  #cooking .bubble:nth-child(1) {
    width: 2.5vh;
    height: 2.5vh;
    margin-top: 2.5vh;
    left: 58%;
    background-color: #454545;
    animation: bubble 2s cubic-bezier(0.53, 0.16, 0.39, 0.96) infinite; }
  #cooking .bubble:nth-child(2) {
    width: 2vh;
    height: 2vh;
    margin-top: 3vh;
    left: 52%;
    background-color: #3d3d3d;
    animation: bubble 2s ease-in-out .35s infinite; }
  #cooking .bubble:nth-child(3) {
    width: 1.5vh;
    height: 1.5vh;
    margin-top: 1.8vh;
    left: 50%;
    background-color: #333;
    animation: bubble 1.5s cubic-bezier(0.53, 0.16, 0.39, 0.96) 0.55s infinite; }
  #cooking .bubble:nth-child(4) {
    width: 1.2vh;
    height: 1.2vh;
    margin-top: 2.7vh;
    left: 56%;
    background-color: #2b2b2b;
    animation: bubble 1.8s cubic-bezier(0.53, 0.16, 0.39, 0.96) 0.9s infinite; }
  #cooking .bubble:nth-child(5) {
    width: 1.1vh;
    height: 1.1vh;
    margin-top: 2.7vh;
    left: 63%;
    background-color: #242424;
    animation: bubble 1.6s ease-in-out 1s infinite; }
  #cooking #area {
    position: absolute;
    top: 140px;
    left: 150px;
    width: 50%;
    height: 50%;
    background-color: transparent;
    transform-origin: 15% 60%;
    animation: flip 2.1s ease-in-out infinite; }
    #cooking #area #sides {
      width: 100%;
      height: 100%;
      position: absolute;
      transform-origin: 15% 60%;
      animation: switchSide 2.1s ease-in-out infinite; }
      #cooking #area #sides #handle {
        width: 35%;
        height: 20%;
        position: absolute;
        right: 80%;
        bottom: 18%;
        background-color: transparent;
        border-top: 1vh solid #333;
        border-left: 1vh solid transparent;
        border-radius: 100%;
        transform: rotate(20deg) rotateX(0deg) scale(1.3, 0.9); }
      #cooking #area #sides #pan {
        position: absolute;
        right: 30%;
        bottom: 20%;
        width: 50%;
        height: 8%;
        background-color: #333;
        border-radius: 0 0 1.4em 1.4em;
        transform-origin: -15% 0; }
    #cooking #area #pancake {
      position: absolute;
      top: 24%;
      width: 100%;
      height: 100%;
      transform: rotateX(85deg);
      animation: jump 2.1s ease-in-out infinite; }
      #cooking #area #pancake #pastry {
        position: absolute;
        right: 37%;
        bottom: 26%;
        width: 40%;
        height: 45%;
        background-color: #333;
        box-shadow: 0 0 3px 0 #333;
        border-radius: 100%;
        transform-origin: -20% 0;
        animation: fly 2.1s ease-in-out infinite; }

@keyframes jump {
  0% {
    top: 24%;
    transform: rotateX(85deg); }
  25% {
    top: 10%;
    transform: rotateX(0deg); }
  50% {
    top: 30%;
    transform: rotateX(85deg); }
  75% {
    transform: rotateX(0deg); }
  100% {
    transform: rotateX(85deg); } }

@keyframes flip {
  0% {
    transform: rotate(0deg); }
  5% {
    transform: rotate(-27deg); }
  30%,
  50% {
    transform: rotate(0deg); }
  55% {
    transform: rotate(27deg); }
  83.3% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(0deg); } }

@keyframes switchSide {
  0% {
    transform: rotateY(0deg); }
  50% {
    transform: rotateY(180deg); }
  100% {
    transform: rotateY(0deg); } }

@keyframes fly {
  0% {
    bottom: 26%;
    transform: rotate(0deg); }
  10% {
    bottom: 40%; }
  50% {
    bottom: 26%;
    transform: rotate(-190deg); }
  80% {
    bottom: 40%; }
  100% {
    bottom: 26%;
    transform: rotate(0deg); } }

@keyframes bubble {
  0% {
    transform: scale(0.15, 0.15);
    top: 50%;
    opacity: 0; }
  50% {
    transform: scale(1.1, 1.1);
    opacity: 1; }
  100% {
    transform: scale(0.33, 0.33);
    top: 30%;
    opacity: 0; } }

@keyframes pulse {
  0% {
    transform: scale(1, 1);
    opacity: .25; }
  50% {
    transform: scale(1.2, 1);
    opacity: 1; }
  100% {
    transform: scale(1, 1);
    opacity: .25; } }

* {
  margin: 0;
  padding: 0;
  cursor: default; }

.App {
  width: 370px;
  height: 100vh;
  position: relative; }
  .App .top_bar {
    display: grid;
    width: 100%;
    height: 38px;
    font-family: 'BMD bold';
    font-size: 14px;
    text-align: center;
    color: #f3f3f3;
    align-items: center;
    text-transform: uppercase;
    letter-spacing: .2em;
    background-color: #303030; }
  .App .nav {
    width: auto;
    height: 50px;
    display: grid;
    grid-template-columns: repeat(5, 20%);
    grid-template-rows: auto;
    align-items: center;
    justify-items: center;
    list-style: none;
    background-color: #F2F2F2;
    border-bottom: 1px solid #EBEBEB; }
    .App .nav li {
      width: 66px;
      height: 42px;
      display: grid;
      grid-template-columns: 100%;
      grid-template-rows: auto;
      font-family: 'BMD bold';
      font-size: 13px;
      text-align: center;
      color: #333;
      align-items: center;
      justify-items: center;
      border-radius: 5px; }
      .App .nav li.active {
        color: #EE586E; }
      .App .nav li.current_day {
        background-color: #EE586E;
        color: #fff; }
      .App .nav li.past_day {
        background-image: url("../img/scribble.png");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 54px 31px;
        pointer-events: none; }
  .App .day {
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: repeat(3, 113px);
    display: none; }
    .App .day.active {
      display: grid; }
    .App .day div {
      padding: 20px 100px 20px 20px;
      border-bottom: 1px solid #ebebeb; }
      .App .day div.meat_lovers {
        background-image: url("../img/icon_meat_lovers.png");
        background-repeat: no-repeat;
        background-position: 95% 15px;
        background-size: 80px 80px; }
      .App .day div.vegetarian {
        background-image: url("../img/icon_vegetarian.png");
        background-repeat: no-repeat;
        background-position: 95% 15px;
        background-size: 80px 80px; }
      .App .day div.salad {
        background-image: url("../img/icon_salad.png");
        background-repeat: no-repeat;
        background-position: 95% 15px;
        background-size: 80px 80px; }
      .App .day div h4 {
        display: inline-block;
        width: auto;
        height: auto;
        font-family: 'BMD bold';
        font-size: 13px;
        text-align: left;
        color: #EE586E;
        padding: 5px;
        margin: 0 0 10px 0;
        border-radius: 5px; }
        .App .day div h4.meat_lovers {
          background-color: #FFD778;
          color: #ED3925; }
        .App .day div h4.vegetarian {
          background-color: #5AC36B;
          color: #fff; }
        .App .day div h4.salad {
          background-color: #0F9CE7;
          color: #fff; }
      .App .day div h3 {
        display: block;
        width: auto;
        height: auto;
        font-family: 'BMD bold';
        font-size: 15px;
        text-align: left;
        color: #000;
        position: relative;
        margin: 10px 0 5px 0; }
        .App .day div h3 span {
          font-family: 'BMD bold';
          font-size: 12px;
          text-align: left;
          color: #EE586E;
          padding: 5px;
          border-radius: 5px;
          background-color: #FDEEF0; }
      .App .day div p {
        font-family: 'BMD regular';
        font-size: 12px;
        text-align: left;
        color: #656565;
        line-height: 1.5em; }
      .App .day div:last-child {
        border-bottom: none; }

.none {
  display: none; }
